import logo from './logo.svg';
import './App.css';
import { Canvas, useFrame } from '@react-three/fiber';
import {MeshWobbleMaterial, OrbitControls} from '@react-three/drei';
import { RoundedBox } from '@react-three/drei';

import Api from './Api';
import { Environment } from '@react-three/drei';
import { Sky } from '@react-three/drei';
import { VRCanvas, Hands, DefaultXRControllers } from '@react-three/xr'

function App() {


  return (
  


    <VRCanvas> 
      <mesh rotation={[-Math.PI / 2, 0, 0]} position={[0,-1, 0]} receiveShadow>
        <planeBufferGeometry attach="geometry" args={[100, 100]} />
        <meshPhysicalMaterial color={'hotpink'} clearcoat={2} clearcoatRoughness={0} roughness={0} metalness={0.25}/>
      </mesh>

   {Api.map(function(el,i){
      

     return(  <RoundedBox key={i} castShadow receiveShadow position={[ (i * 1.2) - Api.length / 1.5,0.5,0]} args={[1, 1, 1,30,30,30]} radius={0.04} smoothness={4} >
       <MeshWobbleMaterial key={i} clearcoat={2} clearcoatRoughness={0} roughness={0} metalness={0.25} speed={i / 15} color={`hsl(97, 99%, ${i * 7}%)`} />
     </RoundedBox> )
   })} 

<Environment files="https://assets.codepen.io/7014830/Studio+%282%29.hdr" />
<Sky distance={450000} sunPosition={[0, 1, 0]} inclination={0} azimuth={0.25} />
<directionalLight castShadow color={'white'} intensity={0.4}/>
    
 
<OrbitControls/>
<DefaultXRControllers />
  <Hands />
</VRCanvas>  
  

  );
}

export default App;
